/* Navbar main container (background, height, etc.) */
.navbar {
  width: 100%;
  display: flex;
  justify-content: center; /* center the inner container */
  height: 60px;
  background: white;
  position: relative;
  z-index: 1000;
  overflow: visible;
}

/* Inner container with padding */
.navbar-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1440px; /* optional max width */
  padding-left: clamp(20px, 8vw, 150px);
  padding-right: clamp(20px, 8vw, 150px);
  box-sizing: border-box;
   overflow: visible;
    overflow: visible;
}



/* ================= LOGO ================= */
.logo {
  flex-shrink: 1;
  transition: all 0.3s;
}

.logo img {
  height: 48px; /* default full view height */
  width: auto;
  cursor: pointer;
  transition: height 0.3s ease, opacity 0.3s ease;
}

/* ================= MENU ================= */
.menu {
  list-style: none;
  display: flex;
  align-items: center;
  gap: 10px; /* default gap */
  margin: 0;
  padding: 0;
  flex-wrap: nowrap; /* prevent wrapping */
   flex-shrink: 0;
   overflow: visible;
}

.menu li {
  position: relative;
  flex-shrink: 0;
}

/* ================= MENU LINKS ================= */
.menu li a {
  text-decoration: none;
  font-weight: bold;
  color: black;
  padding: 6px 10px;
  display: block;
  font-size: 14px;
  transition: color 0.3s;
}

.menu li a:hover {
  color: #01621d;
}

/* ================= DROPDOWN ================= */
.dropdown {
  position: relative;
}

.dropdown-trigger {
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 14px;
}

.dropdown-arrow {
  font-size: 0.8em;
  transition: transform 0.3s;
}

.dropdown:hover .dropdown-arrow {
  transform: rotate(180deg);
}

/* Dropdown menu */
.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  background: white;
  width: 320px;
  list-style: none;
  padding: 8px 0;
  margin: 0;
  border-radius: 6px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
  display: none;
  z-index: 9999; /* dropdown always above */
  border-top: 4px solid #01621d;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.dropdown-menu li a {
  padding: 10px 20px;
  font-weight: 600;
  color: #333;
  border-left: 3px solid transparent;
  transition: all 0.3s;
  font-size: 14px;
}

.dropdown-menu li a:hover {
  background-color: rgba(1, 98, 29, 0.08);
  color: #01621d;
  border-left: 3px solid #01621d;
  padding-left: 17px;
}

/* ================= BUTTONS ================= */
.apply-btn {
  padding: 6px 14px;
  background-color: #01621d;
  color: white;
  border-radius: 6px;
  border: none;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
  white-space: nowrap; /* single line in full view */
  text-align: center;
  font-size: 14px;
}

.apply-btn:hover {
  background-color: #014a14;
  transform: translateY(-1px);
}

/* Admin button */
.admin-btn {
  width: 38px;
  height: 38px;
  background-color: #01621d;
  color: white;
  border-radius: 50%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.3s;
}

.admin-btn:hover {
  background-color: #003366;
  transform: scale(1.05);
}

/* ================= RESPONSIVE ================= */

/* Shrink logo and reduce menu gaps gradually */
@media (max-width: 1200px) {
  .menu {
    gap: 18px;
  }

  .logo img {
    height: 42px;
  }
}

@media (max-width: 1024px) {
  .menu {
    gap: 14px;
  }

  .logo img {
    height: 38px;
  }
}

@media (max-width: 900px) {
  .menu {
    gap: 10px;
  }

  .logo img {
    height: 32px;
  }
}

/* Apply button wrap only when space is very tight */
@media (max-width: 768px) {
  .apply-btn {
    white-space: normal;      /* allow wrapping */
    line-height: 1.1;
    font-size: 13px;
    padding: 4px 8px;
  }

  .apply-btn span {
    display: block;           /* each word on separate line */
    width: 100%;
    text-align: center;
  }

  /* Hide logo if menu is too wide */
  .logo {
    display: none;
  }

  /* Reduce menu gap further */
  .menu {
    gap: 6px;
  }
}

/* Very small screens */
@media (max-width: 500px) {
  .menu li a {
    padding: 4px 6px;
    font-size: 13px;
  }
}
